import React, { useState, useEffect } from "react";
import tw from "twin.macro";
import styled from "styled-components";
import './ProductVariations.css'

import axios from 'axios'
import {baseApiUrl} from '../../config/global'

import { Container as ContainerBase, ContentWithPaddingXl as ContentBase } from "components/Layouts/Layouts";
import NumberFormat from 'react-number-format'

const Container = tw(ContainerBase)`bg-primary-900 text-gray-100 -mx-8 px-8`;
const ContentWithPaddingXl = tw(
  ContentBase
)`relative z-10 mx-auto px-0 py-10 sm:px-6 md:px-8 lg:px-12 xl:px-24 sm:py-20 flex flex-col max-w-screen-xl`;
const PlansContainer = tw.div`mt-16 flex flex-col items-center lg:flex-row lg:items-stretch lg:justify-between text-gray-900 font-medium`;
const Plan = styled.div`
  ${tw`w-full max-w-sm bg-white rounded-lg shadow-sm py-10 px-6 sm:px-10 lg:px-6 lg:py-10 xl:p-10 mx-3 flex flex-col justify-between mt-16 first:mt-0 lg:mt-0 shadow-raised`}
`;
const PlanHeader = styled.div`
  .nameAndFeaturedContainer {
    ${tw`flex flex-wrap flex-col sm:flex-row justify-between items-center`}
  }
  .name {
    ${tw`lg:text-lg xl:text-xl font-bold uppercase tracking-wider mr-3`}
  }
  .featuredText {
    ${tw`text-xs font-bold px-3 rounded py-2 uppercase bg-green-300 text-green-900 leading-none mt-4 sm:mt-0 w-full sm:w-auto text-center`}
  }
  .pricingContainer {
    ${tw`mt-6 flex items-end justify-between`}
    .currentPrice {
      ${tw`text-lg font-bold leading-none`}
      .bigText {
        ${tw`text-3xl font-bold`}
      }
    }
    .oldPrice {
      ${tw`text-gray-500 text-lg line-through hidden sm:block`}
    }
  }
  .description {
    ${tw`mt-8 font-medium text-gray-700 lg:text-sm xl:text-base`}
  }
`;
const PlanFeatures = styled.ul`
  ${tw`mt-10 flex-1 border-t lg:-mx-6 -mx-6 sm:-mx-10 py-10 px-6 sm:px-10 lg:p-6 xl:-mx-10 xl:p-10`}
  .feature {
    ${tw`flex items-start mt-6 first:mt-0`}
    .icon {
      ${tw`w-6 h-6 text-primary-500 flex-shrink-0`}
    }
    .text {
      ${tw`font-semibold text-primary-900 tracking-wide ml-3`}
    }
  }
`;
const PlanAction = tw.div`mt-4`;
const WhiteBackgroundOverlay = tw.div`absolute inset-x-0 bottom-0 h-1/6 lg:h-1/3 bg-white z-0`;

export default (props) => {

  const [variations, setVariations] = useState({})
  
  useEffect(()=>{
    axios.get(`${baseApiUrl}/variations/${props.productId}`)
    .then(res => {
      setVariations(res.data)
    })
  }, [props])

  return (
    <div id="product-variations">
    <Container>
      <div className="styled-container" style={{backgroundColor: props.color}}>
      <ContentWithPaddingXl>
        <div data-editable="true" data-name="article">
          <p id="cts-variations-title" className="subheading">{variations.subtitulo_variacoes}</p>
          <h1 id="cts-variations-subtitle" className="heading">{variations.titulo_variacoes}</h1>
        </div>
        <PlansContainer>
          {variations.variations && variations.variations.map((variation, index) => (
            <Plan key={index} featured={variation.mais_vendido === 'Sim'}>
                <PlanHeader>
                  <span className="nameAndFeaturedContainer">
                    <span className="name">{variation.titulo}</span>
                    {variation.mais_vendido === 'Sim' && <span className="featuredText">MAIS VENDIDO</span>}
                  </span>
                  <div className="pricingContainer">
                    <span className="currentPrice">
                      <span className="bigText">
                        <NumberFormat value={variation.por} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} prefix={'R$ '}/>
                      </span>
                    </span>
                    <span className="oldPrice">
                      <NumberFormat value={variation.de} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} prefix={'R$ '}/>
                    </span>
                  </div>
                  <p className="description">ou {variation.numero_de_parcelas}x de <NumberFormat value={variation.valor_parcela} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} prefix={'R$ '}/></p>
                </PlanHeader>
                <PlanFeatures>
                  <img src={`https://app.shopifacil.com.br/produtos-variacoes/foto/${variation.id}.${variation.foto}`} alt="" />
                </PlanFeatures>
                <PlanAction>
                  {variation.mais_vendido === 'Sim' &&
                  <button style={{backgroundColor: props.color, borderColor: props.color}} className="btn btn-primary checkout-button" as="a" href={variation.link_checkout}>
                    COMPRAR AGORA!
                  </button>}
                  {variation.mais_vendido === 'No' &&
                  <button className="btn btn-primary checkout-button" as="a" href={variation.link_checkout}
                  style={{backgroundColor: '#fff', border: `1px solid ${props.color}`, color: props.color}}>
                    COMPRAR AGORA!
                  </button>}
                </PlanAction>
            </Plan>
          ))}
        </PlansContainer>
      </ContentWithPaddingXl>
      </div>
      <WhiteBackgroundOverlay />
    </Container>
    </div>
  );
};
