import React, {useState, useEffect} from 'react'

import axios from 'axios'
import { baseApiUrl } from '../../config/global'

export default function Slider(props){

    const [images,setImages] = useState([])

    useEffect(()=>{
        axios.get(`${baseApiUrl}/product-slider-image/${props.productId}`)
        .then(res=>{
            setImages(res.data)
        })
    }, [props])

    return (
        <section>
            <div className="container">
                <div className="row">
                    <div className="text-center">
                        <div id="carousel-726829999" className="carousel slide mb-1">
                            <div className="carousel-inner">
                                {images.map((image, index) =>
                                    <div key={index} className={index === 0 ? 'item carousel-item active' : 'item carousel-item'}>
                                        <img src={`https://app.shopifacil.com.br/produtos-fotos/foto/${image.id}.${image.foto}`} alt="Hills"/>
                                    </div>
                                )}
                            </div>
                        </div>
                        <div className="product-thumbnails list-unstyled d-flex">
                            {images.map((image, index) =>
                                <li key={index} data-target="#carousel-726829999" data-slide-to={index} className="">
                                    <img className="mt-2 mr-2 img-fluid selected-img" src={`https://app.shopifacil.com.br/produtos-fotos/foto/${image.id}.${image.foto}`} alt="image01"/>
                                </li>
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}