import React, {useState, useEffect} from 'react'
import './Positives.css'

import axios from 'axios'
import {baseApiUrl} from '../../config/global'

export default function Positives(props){

    const [positivesData,setPositivesData] = useState({})
    const [positives, setPositives] = useState([])

    useEffect(()=>{
        axios.get(`${baseApiUrl}/positives/${props.productId}`)
        .then(res=>{
            setPositivesData(res.data)
            setPositives(res.data.positives)
        })
    }, [props])

    return (
        <div id="positives">
            <div data-editable="true" data-name="article">
                <h1 id="cts-positives-title" className="heading">{positivesData.titulo_beneficios}</h1>
                <p id="cts-positives-subtitle" className="subheading" style={{color: props.color}}>{positivesData.subtitulo_beneficios}</p>
            </div>
            <div className="positives-cards">
            {positives.map((positive, index) =>
                <div key={index} className="positives-card">
                    <img src={`https://app.shopifacil.com.br/produtos-beneficios/foto/${positive.id}.${positive.foto}`} alt="" />
                    <div cts-positive-container="true" cts-positive-value={positive.id} data-editable="true" data-name="article">
                        <p cts-positive-id="cts-positive-title" className="positives-title">{positive.titulo}</p>
                        <p cts-positive-id="cts-positive-subtitle" className="positives-subtitle">{positive.subtitulo}</p>
                    </div>
                </div>
            )}
            </div>
        </div>
    )
}