import './Section1.css'
import React, { useState, useEffect } from "react";
import tw from "twin.macro";
import axios from 'axios'
import {baseApiUrl,pageTest} from '../../config/global'

import { SectionHeading } from "components/Headings/Headings";
import { Container as ContainerBase, ContentWithPaddingXl } from "components/Layouts/Layouts";

const Container = tw(ContainerBase)`bg-primary-900 text-gray-100 -mx-8 px-8`;
const HeadingContainer = tw.div``;
const Heading = tw(SectionHeading)`sm:text-3xl md:text-4xl lg:text-5xl`;

const ImageContainer = tw.div`flex items-center justify-center`

export default (props) => {

  const [deadline, setDeadline] = useState()
  const getSectionData = () => axios.get(`${baseApiUrl}/section/1/${pageTest}`).then(res =>{
    setDeadline(res.data.prazo_de_entrega)
  })
  useEffect(()=>{ getSectionData() },[])

  return (
    <Container>
      <div className="styled-container"style={{backgroundColor: props.color}}>
        <ContentWithPaddingXl>
          <HeadingContainer>
            <Heading>Importante</Heading>
            {deadline &&
            <div className="deadline" data-editable="true" data-name="article">
              <p id="cts-section-deadline">{deadline}</p>
            </div>}
          </HeadingContainer>
          <ImageContainer>
            <img className="section-img" src="https://shopifacil.com.br/assets/img/bandeiras.png" alt="bandeiras de carto aceitas" />
          </ImageContainer>
        </ContentWithPaddingXl>
      </div>
    </Container>
  );
};
