import React, { useEffect, useState } from "react";
import tw from "twin.macro";
import axios from 'axios'
import { baseApiUrl, pageTest } from '../../config/global'

import {Container as ContainerBase } from "../Layouts/Layouts"

const Container = tw(ContainerBase)`bg-gray-900 text-gray-100 -mx-8 -mb-8`
const Content = tw.div`max-w-screen-xl mx-auto py-20 lg:py-10`;
const Row = tw.div`flex items-center justify-center flex-col px-8`
const LinksContainer = tw.div`mt-2 font-medium flex flex-wrap justify-center items-center flex-col sm:flex-row`
const Link = tw.a`border-b-2 border-transparent hocus:text-gray-300 hocus:border-gray-300 pb-1 transition duration-300 mt-2 mx-4`;
const CopyrightText = tw.p`text-center mt-10 font-medium tracking-wide text-sm text-gray-600`

export default () => {
  const [data, setData] = useState({})
  const getSectionData = () => axios.get(`${baseApiUrl}/section/3/${pageTest}`).then(res =>{
      setData(res.data)
  })
  useEffect(()=>{ getSectionData() },[])

  return (
    <Container>
      <Content>
        <Row>
          <LinksContainer>
            <Link href="/sobre">Sobre</Link>
            <Link href="/contato">Contato</Link>
            <Link href="/envios-e-prazos">Envios e Prazos</Link>
            <Link href="/politica-de-privacidade">Poltica de Privacidade</Link>
            <Link href="/termos-de-uso">Termos de Uso</Link>
            <Link href="/duvidas-frequentes">Perguntas Frequentes</Link>
          </LinksContainer>
          <CopyrightText>
            &copy; {data.nome} | Todos os direitos reservados.
          </CopyrightText>
        </Row>
      </Content>
    </Container>
  );
};
