import React, {useState, useEffect} from "react";
import styled from "styled-components";
import tw from "twin.macro";

import axios from 'axios'
import {baseApiUrl} from '../../config/global'

import { ReactComponent as SvgDecoratorBlob3 } from "../../images/svg-decorator-blob-3.svg";

const Container = tw.div`relative bg-gray-100 -mx-8 -mb-8 px-8`;
const ThreeColumnContainer = styled.div`
  ${tw`flex flex-col items-center md:items-stretch md:flex-row flex-wrap md:justify-center max-w-screen-xl mx-auto py-20 md:py-24 px-8`}
`;
const DecoratorBlob = styled(SvgDecoratorBlob3)`
  ${tw`pointer-events-none absolute right-0 bottom-0 w-64 opacity-25 transform translate-x-32 translate-y-48 `}
`;

export default (props) => {

  const [details, setDetails] = useState('')

  useEffect(()=>{
    axios.get(`${baseApiUrl}/details/${props.productId}`)
    .then(res=>{
      setDetails(res.data)
    })
  }, [props])

  return (
    <Container>
      <ThreeColumnContainer>
        <div data-editable="true" data-name="article" id="cts-longdescription-details" dangerouslySetInnerHTML={{__html: details}}></div>
      </ThreeColumnContainer>
      <DecoratorBlob />
    </Container>
  );
};
