import './Section2.css'

import React, { useState, useEffect } from 'react'

import axios from 'axios'
import { baseApiUrl, pageTest } from '../../config/global'

export default function Section(props){

    const [data, setData] = useState({})

    useEffect(()=>{
        axios.get(`${baseApiUrl}/section/2/${pageTest}`)
        .then(res =>{
            setData(res.data)
        })
    },[])

    return (
        <div className="section-secondary">
            <img src={`https://app.shopifacil.com.br/lojas/logomarca-rodape/${data.id}.${data.logomarca_rodape}`} alt="logo" />
            <div>
                <span>
                CNPJ: 
                </span>
                {data.cpfcnpj && 
                <div className="section-data" data-editable="true" data-name="article">
                    <p id="cts-section-cpfcnpj">{data.cpfcnpj}</p>
                </div>}
            </div>
            <div>
                <span>
                Email: 
                </span>
                {data.e_mail && 
                <div className="section-data" data-editable="true" data-name="article">
                    <p id="cts-section-email">{data.e_mail}</p>
                </div>}
            </div>
            <div>
                <span>
                Whatsapp: 
                </span>
                {data.whatsapp && 
                <div className="section-data" data-editable="true" data-name="article">
                    <p id="cts-section-whatsapp">{data.whatsapp}</p>
                </div>} 
            </div>
            <p>
                Usamos cookies para garantir a voc a melhor experincia no nosso site. Se continuar navegando sem 
                <br/>alterar as suas configuraes, supomos que voc aceita esta prtica. Se desejar,
                <br/>voc pode alterar as configuraes de cookies a qualquer momento <a href="/termos-de-uso" style={{color: props.color, fontWeight: 'bold'}}>Clique aqui e saiba mais</a>.
            </p>
        </div>
    )
}