import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { baseApiUrl, pageTest } from '../config/global'
import './Subpage.css'

import AnimationRevealPage from "../components/AnimationRevealPage/AnimationRevealPage";
import Header from '../components/Headers/Header3'
import Section1 from '../components/Sections/Section1'
import Section2 from '../components/Sections/Section2'
import Footer from '../components/Footer/Footer'

export default function Subpage(){

    const [text, setText] = useState('')
    const [tab, setTab] = useState('')
    const [clientName, setClientName] = useState('')
    const [clientMail, setClientMail] = useState('')
    const [subject, setSubject] = useState('')
    const [message, setMessage] = useState('')

    const getPage = () => {
        const url = window.location.href.split('/')
        const page = url[url.length-1]
        setTab(page)
        axios.get(`${baseApiUrl}/page/${page}/${pageTest}`).then(res => {
            res.data ? setText(res.data.texto) : setText({})
        })
    } 

    const sendMail = () => {
        setClientName('')
        setClientMail('')
        setSubject('')
        setMessage('')
        axios.post(`${baseApiUrl}/mail/${pageTest}`, {clientName, clientMail, subject, message}).then(res => {
            console.log(res.data)
        })
    }

    useEffect(()=>{getPage()}, [])

    return(
        <AnimationRevealPage>
            <Header />
            <div className="main">
                <div className="main-content" dangerouslySetInnerHTML={{__html: text}} />
                {tab === 'contato' && 
                <div className="form">
                    <div className="input-group">
                        <input type="text" name="name" placeholder="Nome" value={clientName} onChange={e => setClientName(e.target.value)} />
                        <input type="text" name="email" placeholder="Email" value={clientMail} onChange={e => setClientMail(e.target.value)} />
                    </div>
                    <div className="input-group">
                        <input type="text" name="subject" placeholder="Assunto" value={subject} onChange={e => setSubject(e.target.value)} />
                    </div>
                    <div className="input-group">
                        <input type="text" name="message" placeholder="Mensagem" value={message} onChange={e => setMessage(e.target.value)} />
                    </div>
                    <div className="input-group">
                        <button onClick={e => sendMail()} name="sendMail">ENVIAR MENSAGEM</button>
                    </div>
                </div>
                }
            </div>
            <Section1 />
            <Section2 />
            <Footer />
        </AnimationRevealPage>
    )
}