import React, { useState, useEffect } from "react";
import NumberFormat from 'react-number-format'
import axios from 'axios'
import { motion } from "framer-motion";
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro";
import './ProductsTabs.css'

import { Container, ContentWithPaddingXl } from "components/Layouts/Layouts";
import { SectionHeading } from "components/Headings/Headings";
import { ReactComponent as SvgDecoratorBlob1 } from "images/svg-decorator-blob-5.svg";
import { ReactComponent as SvgDecoratorBlob2 } from "images/svg-decorator-blob-7.svg";

import { baseApiUrl, pageTest } from '../../config/global'

const HeaderRow = tw.div`flex justify-between items-center flex-col xl:flex-col`;
const Header = tw(SectionHeading)`mb-5`;
const TabsControl = tw.div`flex flex-wrap bg-gray-200 px-2 py-2 rounded leading-none mt-12 xl:mt-0`;
const TabContent = tw(motion.div)`mt-6 flex flex-wrap sm:-mr-10 md:-mr-6 lg:-mr-12`;
const CardContainer = tw.div`mt-10 w-full sm:w-1/2 md:w-1/3 lg:w-1/4 sm:pr-10 md:pr-6 lg:pr-12`;
const Card = tw(motion.a)`pb-4 bg-gray-200 rounded-b block max-w-xs mx-auto sm:max-w-none sm:mx-0`;
const CardText = tw.div`p-4 text-gray-900`;
const CardContent = tw.p`mt-1 text-sm font-medium text-gray-600`;
const CardImageContainer = styled.div`
  ${props => css`background-image: url("${props.imageSrc}");`}
  ${tw`h-56 xl:h-64 bg-center bg-cover relative rounded-t`}
`;
const CardHoverOverlay = styled(motion.div)`
  background-color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  ${tw`absolute inset-0 flex justify-center items-center`}
`;
const DecoratorBlob1 = styled(SvgDecoratorBlob1)`
  ${tw`pointer-events-none -z-20 absolute right-0 top-0 h-64 w-64 opacity-15 transform translate-x-2/3 -translate-y-12 text-pink-400`}
`;
const DecoratorBlob2 = styled(SvgDecoratorBlob2)`
  ${tw`pointer-events-none -z-20 absolute left-0 bottom-0 h-80 w-80 opacity-15 transform -translate-x-2/3 text-primary-500`}
`;

export default (props) => {

  const [activeTab, setActiveTab] = useState('')
  const [tabs, setTabs] = useState([])
  const [products, setProducts] = useState([])
  const [activePage, setActivePage] = useState(1)
  const [pages, setPages] = useState([])

  const getProductsByCategory = (categoryId, page) => {
    axios.get(`${baseApiUrl}/products/${pageTest}/${categoryId}/${page}`)
    .then(res => {
      setProducts(res.data.productsData)
      setPages(res.data.pages)
    })
  }

  const handleChangePage = (pageNumber) => {
    setActivePage(pageNumber)
    getProductsByCategory(activeTab, pageNumber)
  } 

  const handleChangeCategory = (tabId) => {
    setProducts([])
    setActiveTab(tabId)
    getProductsByCategory(tabId, 1)
  } 

  useEffect(()=>{
    axios.get(`${baseApiUrl}/categories/${pageTest}`).then(res =>{
      setActiveTab(-1)
      getProductsByCategory(-1, 1)
      setTabs([{id: -1, nome: 'TODAS'}, ...res.data])
    })
  },[])

  return (
    <div id="product-tabs">
    <Container>
      <ContentWithPaddingXl>
        <HeaderRow>
          <Header>Escolha sua categoria de menus</Header>
          <TabsControl>
            {tabs.map(tab =>
              <div className={`tab-control`} style={tab.id === activeTab ? {backgroundColor: props.color, color: 'white', borderRadius: '4px'} : {}}
              key={tab.id} active={tab.id === activeTab} onClick={() => handleChangeCategory(tab.id)}>
                {tab.nome}
              </div>
            )}
          </TabsControl>
        </HeaderRow>

        {tabs.map(tab => (
          <TabContent
            key={tab.id}
            variants={{
              current: {
                opacity: 1,
                scale:1,
                display: "flex",
              },
              hidden: {
                opacity: 0,
                scale:0.8,
                display: "none",
              }
            }}
            transition={{ duration: 0.4 }}
            initial={activeTab === tab.id ? "current" : "hidden"}
            animate={activeTab === tab.id ? "current" : "hidden"}
          >
            {products.map(product => (
              <CardContainer key={product.id}>
                <Card className="group" href={`/produto/${product.id}`} initial="rest" whileHover="hover" animate="rest">
                  <CardImageContainer imageSrc={`https://app.shopifacil.com.br/produtos-fotos/foto/${product.photo}`} >
                    <CardHoverOverlay
                      variants={{
                        hover: {
                          opacity: 1,
                          height: "auto"
                        },
                        rest: {
                          opacity: 0,
                          height: 0
                        }
                      }}
                      transition={{ duration: 0.3 }}
                    >
                    </CardHoverOverlay>
                  </CardImageContainer>
                  <CardText>
                    <h5 className="product-title" style={{color: props.color}}>{product.titulo}</h5>
                    <CardContent>{product.subtitulo}</CardContent>
                    <p className="productPrice">
                      <span className="productDe">De <NumberFormat value={product.variations.de} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} prefix={'R$  '}/> por</span>
                      <span className="productPor"><NumberFormat value={product.variations.por} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} prefix={'R$ '}/></span>
                      <span className="productNum">ou {product.variations.numero_de_parcelas}x de <NumberFormat value={product.variations.valor_parcela} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} prefix={'R$ '}/></span>
                    </p>
                  </CardText>
                  <button className="btn btn-lg text-center" style={{backgroundColor: props.color, color: 'white'}}>SAIBA MAIS</button>
                </Card>
              </CardContainer>
            ))}
          </TabContent>
        ))}
        <div className="pagination">
          {activePage > 1 &&
          <a className="previous" href="#product-tabs" style={{borderColor: props.color}} onClick={e => handleChangePage(activePage-1)}>Anterior</a>}
          <span style={{borderColor: props.color}}>{activePage}/{pages.length}</span>
          {activePage < pages.length &&
          <a className="next" href="#product-tabs" style={{borderColor: props.color}} onClick={e => handleChangePage(activePage+1)}>Prximo</a>}
        </div>
      </ContentWithPaddingXl>
      <DecoratorBlob1 />
      <DecoratorBlob2 />
    </Container>
    </div>
  );
};