import "tailwindcss/dist/base.css";
import "styles/globalStyles.css";
import React from "react";

import './App.css'

import { Switch, Route, Router } from 'react-router-dom'
import { createBrowserHistory } from 'history';

import Store from './pages/Store'
import Product from './pages/Product'
import Subpage from './pages/Subpage'

const history = createBrowserHistory();

export default function App() {
  return (
    <Router history={history}>
      <Switch>
        <Route exact path="/" component={Store} />
        <Route path="/produto" component={Product} />
        <Route path="/sobre" component={Subpage} />
        <Route path="/contato" component={Subpage} />
        <Route path="/envios-e-prazos" component={Subpage} />
        <Route path="/politica-de-privacidade" component={Subpage} />
        <Route path="/termos-de-uso" component={Subpage} />
        <Route path="/duvidas-frequentes" component={Subpage} />
      </Switch>
    </Router>
  );
}