import React, {useEffect, useState} from "react"

import axios from 'axios'
import {baseApiUrl, pageTest} from '../config/global'

import AnimationRevealPage from "../components/AnimationRevealPage/AnimationRevealPage"
import Header from '../components/Headers/Header3'
import CarouselView from '../components/CarouselView/CarouselView'
import ProductsTabs from '../components/ProductsTabs/ProductsTabs'
import Section1 from '../components/Sections/Section1'
import Section2 from '../components/Sections/Section2'
import Footer from '../components/Footer/Footer'

export default function App() {

  const [color, setColor] = useState('')

  useEffect(()=>{
    axios.get(`${baseApiUrl}/store/${pageTest}`)
    .then(res => {
      setColor(res.data.cor)
    })
  }, [])

  return (
    <AnimationRevealPage>
      <Header color={color} />
      <CarouselView />
      <ProductsTabs color={color} />
      <Section1 color={color} />
      <Section2 color={color} />
      <Footer />
    </AnimationRevealPage>
  );
}