import React, { useState, useEffect } from "react";
import tw from "twin.macro";
import styled from "styled-components";
import './ProductVideo.css'

import axios from 'axios'
import { baseApiUrl } from '../../config/global'

import ResponsiveVideoEmbed from "../ResponsiveVideoEmbed/ResponsiveVideoEmbed.jsx";

const Container = styled.div`
  ${tw`relative -mx-8 bg-center bg-cover`}
  background-image: url("https://images.unsplash.com/photo-1522071901873-411886a10004?ixlib=rb-1.2.1&auto=format&fit=crop&w=1920&q=80");
`;

const HeroContainer = tw.div`z-20 relative px-4 sm:px-8 max-w-screen-xl mx-auto`;
const TwoColumn = tw.div`pt-24 pb-32 px-4 flex justify-between items-center flex-col lg:flex-row`;
const LeftColumn = tw.div`flex flex-col items-center lg:block`;
const RightColumn = tw.div`w-full sm:w-5/6 lg:w-1/2 mt-16 lg:mt-0 lg:pl-8`;
const StyledResponsiveVideoEmbed = styled(ResponsiveVideoEmbed)`
  padding-bottom: 56.25% !important;
  padding-top: 0px !important;
  ${tw`rounded`}
  iframe {
    ${tw`rounded bg-black shadow-xl`}
  }
`;

export default (props) => {

  const [videoData, setVideoData] = useState({})

  useEffect(()=>{
    axios.get(`${baseApiUrl}/product-video/${props.productId}`)
    .then(res=>{
      setVideoData(res.data)
    })
  }, [props])

  return (
    <Container>
      <div className="styled-container" style={{backgroundColor: props.color}}>
      {videoData.imagem_fundo && <div className="overlay" style={{backgroundColor: props.color}} />}
      <HeroContainer>
        <TwoColumn>
          <LeftColumn>
            <div data-editable="true" data-name="article" className="productvideo-content">
              <p className="productvideo-title" id="cts-productvideo-title">{videoData.titulo}</p>
              <p className="productvideo-subtitle" id="cts-productvideo-subtitle">{videoData.subtitulo}</p>
            </div>
            <br/>
            <button className="btn btn-primary btn-lg" style={{padding: '18px 48px', backgroundColor: '#fff', color: props.color, borderColor: props.color, fontWeight: 'bold'}}>EU QUERO AGORA</button>
          </LeftColumn>
          <RightColumn>
            <StyledResponsiveVideoEmbed
              url={videoData.video}
              background="transparent"
            />
          </RightColumn>
        </TwoColumn>
      </HeroContainer>
      </div>
    </Container>
  );
};
