import React, {useState, useEffect} from "react";
import styled from "styled-components";
import tw from "twin.macro";
import { css } from "styled-components/macro"; //eslint-disable-line
import './Testimonials.css'
import { ContentWithPaddingXl, Container } from "components/Layouts/Layouts";
import { ReactComponent as SvgDecoratorBlob1 } from "images/svg-decorator-blob-7.svg";
import { ReactComponent as SvgDecoratorBlob2 } from "images/svg-decorator-blob-8.svg";

import axios from 'axios'
import {baseApiUrl} from '../../config/global'

const Testimonials = tw.div`flex flex-col lg:flex-row items-start`;
const TestimonialContainer = tw.div`mt-16 mx-4 lg:w-1/3 shadow-xs h-auto`;
const Testimonial = tw.div`px-4 pt-4 text-center max-w-xs mx-auto flex flex-col items-center`;
const Image = tw.img`w-20 h-20 rounded-full`;

const DecoratorBlob1 = styled(SvgDecoratorBlob1)`
  ${tw`pointer-events-none -z-20 absolute left-0 top-0 h-56 w-56 opacity-15 transform -translate-x-2/3 -translate-y-12 text-teal-400`}
`;
const DecoratorBlob2 = styled(SvgDecoratorBlob2)`
  ${tw`pointer-events-none -z-20 absolute right-0 bottom-0 h-64 w-64 opacity-15 transform translate-x-2/3 text-yellow-500`}
`;

export default (props) => {
  const renderStars = (stars) => {
    const markeds = []
    const nonMarkeds = []
    for(let i = 0; i < stars; ++i){
      markeds.push(<i key={i} className="fa fa-star marked-star"></i>)
    }
    for(let i = 0; i < (5-stars); ++i){
      nonMarkeds.push(<i key={i} className="fa fa-star"></i>)
    }
    return(
      <>
        {markeds}
        {nonMarkeds}
      </>
    )
  }

  const [testimonials, setTestimonials] = useState([])
  const [testimonialsData, setTestimonialsData] = useState([])

  useEffect(()=>{
    axios.get(`${baseApiUrl}/testimonials/${props.productId}`)
    .then(res=>{
      setTestimonialsData(res.data)
      setTestimonials(res.data.testimonials)
    })
  }, [props])

  return (
    <Container>
      <ContentWithPaddingXl>
        <div data-editable="true" data-name="article">
          <h1 id="cts-testimonials-title" className="heading">{testimonialsData.titulo_depoimentos}</h1>
          <p id="cts-testimonials-subtitle" className="subheading" style={{color: props.color}}>{testimonialsData.subtitulo_depoimentos}</p>
        </div>
        <Testimonials>
          {testimonials.map((testimonial, index) => (
            <TestimonialContainer key={index}>
              <Testimonial>
                 <div className="stars">
                  {renderStars(testimonial.id_estrelas || 0)}
                </div>
                <>
                  {testimonial.foto_produto && 
                  <img src={`https://app.shopifacil.com.br/produtos-depoimentos/foto-produto/${testimonial.id}.${testimonial.foto_produto}`} alt="img" />}
                  {testimonial.videoUrl && 
                  <iframe title={index} src={testimonial.video}></iframe>}
                </>
                <div cts-testimonial-container="true" cts-testimonial-value={testimonial.id} data-editable="true" data-name="article">
                  <p className="quote" cts-testimonial-id="cts-testimonial-text">"{testimonial.depoimento}"</p>
                </div>
                {testimonial.foto_cliente &&
                <Image src={`https://app.shopifacil.com.br/produtos-depoimentos/foto-cliente/${testimonial.id}.${testimonial.foto_cliente}`} />}
                <div data-editable="true" data-name="article">
                  <p className="client" cts-testimonial-id="cts-testimonial-client">{testimonial.cliente}</p>
                  <p className="profission-city" cts-testimonial-id="cts-testimonial-profissionCity">{testimonial.profissao__cidade}</p>
                </div>
              </Testimonial>
            </TestimonialContainer>
          ))}
        </Testimonials>
      </ContentWithPaddingXl>

      <DecoratorBlob1 />
      <DecoratorBlob2 />
    </Container>
  );
};
