import React, { useState, useEffect } from "react";
import './Alert.css'

import axios from 'axios'
import { baseApiUrl, pageTest } from '../../config/global'

import { Container as ContainerBase } from "components/Layouts/Layouts";

export default (props) => {

  const [data, setData] = useState({})

  useEffect(()=>{
    axios.get(`${baseApiUrl}/section/2/${pageTest}`)
    .then(res =>{
        setData(res.data)
    })
  },[])

  return (
    <ContainerBase>
      <div className="alert">
        <p style={{color: props.color}}>{
          `
          ATENO: ltimas unidades, caso emita boleto bancrio envie o 
          comprovante de pagamento o mais rpido possvel para nosso whatsapp: 
          ${data.whatsapp} ou e-mail: ${data.e_mail} para garantir o seu. 
          ATUALIZADO DIA: 28/06/2020
          `
          }</p>
      </div>
    </ContainerBase>
  );
};
