import React, {useEffect,useState} from 'react'

import axios from 'axios'
import { baseApiUrl, pageTest } from '../../config/global'

import './Header1.css'

export default function Header1(props){

  const [text,setText] = useState('')

  const getHeaderData = () => {
    axios.get(`${baseApiUrl}/freight/${pageTest}`)
      .then(res=>{
        setText(res.data)
      })
  }

  useEffect(()=>{getHeaderData()}, [])

  return (
    <div data-editable="true" data-name="article" className="header1 header2" style={{backgroundColor: props.color}}>
      <p id="cts-freight-text">{text}</p>
    </div>
  )
}