import React, { useState, useEffect } from "react";

import axios from 'axios'
import { baseApiUrl } from '../../config/global'

import styled from "styled-components";
import tw from "twin.macro";

import { motion } from "framer-motion";

import { Container, ContentWithPaddingXl } from "components/Layouts/Layouts";
import { ReactComponent as ChevronDownIcon } from "feather-icons/dist/icons/chevron-down.svg";

const Column = tw.div`flex flex-col items-center`;
const FAQSContainer = tw.dl`mt-12 max-w-4xl relative`;
const FAQ = tw.div`cursor-pointer select-none mt-5 px-8 sm:px-10 py-5 sm:py-4 rounded-lg text-gray-800 hover:text-gray-900 bg-gray-200 hover:bg-gray-300 transition duration-300`;
const Question = tw.dt`flex justify-between items-center`;
const Answer = motion.custom(tw.dd`pointer-events-none text-sm sm:text-base leading-relaxed`);
const QuestionToggleIcon = motion.custom(styled.span`
  ${tw`ml-2 transition duration-300`}
  svg {
    ${tw`w-6 h-6`}
  }
`);

export default (props) => {
  const [activeQuestionIndex, setActiveQuestionIndex] = useState(null);
  const [questions, setQuestions] = useState({})

  const toggleQuestion = questionIndex => {
    if (activeQuestionIndex === questionIndex) setActiveQuestionIndex(null);
    else setActiveQuestionIndex(questionIndex);
  };

  useEffect(()=>{
    axios.get(`${baseApiUrl}/questions/${props.productId}`)
    .then(res => {
      setQuestions(res.data)
    })
  }, [props])

  return (
    <Container>
      <ContentWithPaddingXl>
        <Column>
          <div className="deadline" data-editable="true" data-name="article">
            <p className="subheading" id="cts-commonquestions-subtitle" style={{fontWeight: 'bold', color: props.color}}>{questions.subtitulo_perguntas_frequentes}</p>
            <h1 className="heading" id="cts-commonquestions-title">{questions.titulo_perguntas_frequentes}</h1>
          </div>
          <FAQSContainer>
            {questions.questions && questions.questions.map((question, index) => (
              <FAQ
                key={index}
                onClick={() => {
                  toggleQuestion(index);
                }}
                className="group"
              >
                <Question>
                  <div cts-commonquestions-container="true" cts-commonquestions-value={question.id} data-editable="true" data-name="article">
                    <p cts-commonquestions-id="cts-commonquestions-question">{question.pergunta}</p>
                  </div>
                  <QuestionToggleIcon
                    variants={{
                      collapsed: { rotate: 0 },
                      open: { rotate: -180 }
                    }}
                    initial="collapsed"
                    animate={activeQuestionIndex === index ? "open" : "collapsed"}
                    transition={{ duration: 0.02, ease: [0.04, 0.62, 0.23, 0.98] }}
                  >
                    <ChevronDownIcon />
                  </QuestionToggleIcon>
                </Question>
                <Answer
                  variants={{
                    open: { opacity: 1, height: "auto", marginTop: "16px" },
                    collapsed: { opacity: 0, height: 0, marginTop: "0px" }
                  }}
                  initial="collapsed"
                  animate={activeQuestionIndex === index ? "open" : "collapsed"}
                  transition={{ duration: 0.3, ease: [0.04, 0.62, 0.23, 0.98] }}
                >
                  <div data-editable="true" data-name="article">
                    <p cts-commonquestions-id="cts-commonquestions-answer">{question.resposta}</p>
                  </div>
                </Answer>
              </FAQ>
            ))}
          </FAQSContainer>
        </Column>
      </ContentWithPaddingXl>

    </Container>
  );
};
