import React, { useState, useEffect } from "react";
import tw from "twin.macro";
import styled from "styled-components";
import './ProductSlider.css'

import axios from 'axios'
import {baseApiUrl, pageTest} from '../../config/global'

import Slider from '../Slider/Slider'

const Container = tw.div`relative`;
const TwoColumn = tw.div`flex flex-col md:flex-row justify-between max-w-screen-xl mx-auto py-20 md:py-24`;
const Column = tw.div`w-full max-w-md mx-auto md:max-w-none md:mx-0`;
const ImageColumn = tw(Column)`md:w-5/12 flex-shrink-0 h-auto md:h-auto relative`;
const TextContent = tw.div`lg:pb-8 text-center md:text-left flex flex-col items-center justify-center`;
const TextColumn = styled(Column)(props => [
  tw`md:w-7/12 mt-16 md:mt-0`,
  props.textOnLeft ? tw`md:mr-12 lg:mr-16 md:order-first` : tw`md:ml-12 lg:ml-16 md:order-last`
]);

export default (props) => {

  const imageInsideDiv = true
  const textOnLeft = false

  const [timer, setTimer] = useState('00:00:00')
  const [h, setH] = useState(10)
  const [m, setM] = useState(2)
  const [s, setS] = useState(10)

  function format(i){
    return i < 10 ? `0${i}` : i
  }

  function updateTimer(){
    if(s === 0){
      setS(59)
      setM(m - 1)
    }
    else if(m === 0){
      setM(59)
      setH(h - 1)
    }
    else{
      setS(s - 1)
    }
    setTimer(`${format(h)}:${format(m)}:${format(s)}`)
  }

  setTimeout(()=>{
    updateTimer()
  },1000)

  const [productData,setProductData] = useState({})

  useEffect(()=>{
    axios.get(`${baseApiUrl}/product-slider-text/${pageTest}/${props.productId}`)
    .then(res=>{
      setProductData(res.data)
    })
  }, [props])

  return (
    <Container>
      <TwoColumn css={!imageInsideDiv && tw`md:items-center`}>
        <ImageColumn>
          <Slider productId={props.productId} />
        </ImageColumn>
        <TextColumn textOnLeft={textOnLeft}>
          <TextContent>
            <div data-editable="true" data-name="article">
              <h2 id="cts-productslider-title" className="heading" style={{marginBottom: '24px', color: props.color}}>{productData.titulo}</h2>
            </div>
            <div data-editable="true" data-name="article" id="cts-productslider-description" dangerouslySetInnerHTML={{__html: productData.descricao_curta}}></div>
            <button style={{backgroundColor: props.color, borderColor: props.color, padding: '12px 48px'}} className="btn btn-primary btn-lg buy-button" as="a" href="#product-variations">
              EU QUERO AGORA!
            </button>
            {productData.exibir_selo_de_seguranca === 'Sim' && 
              <img className="secureImage" src={`https://app.shopifacil.com.br/lojas/selo-de-seguranca/${productData.securityImage}`} alt="Imagem de segurana" />
            }
            {productData.exibir_contagem_regressiva === 'Sim' && 
            <div className="timer">
              <span className="timer-title">O DESCONTO ACABA EM:</span>
              <span className="timer-numbers">{timer}</span>
            </div>
            }
          </TextContent>
        </TextColumn>
      </TwoColumn>
    </Container>
  );
};
