import React, { useState, useEffect } from "react";

import axios from "axios";
import {baseApiUrl,pageTest} from '../config/global'

import AnimationRevealPage from "components/AnimationRevealPage/AnimationRevealPage";
import Header1 from "components/Headers/Header1"
import Header2 from "components/Headers/Header2" 
import ProductSlider from "components/ProductSlider/ProductSlider";
import Alert from "components/Alert/Alert"
import ProductVideo from "components/ProductVideo/ProductVideo"
import Positives from "components/Positives/Positives"
import BuyButton from "components/BuyButton/BuyButton"
import LongDescription from "components/LongDescription/LongDescription"
import Testimonials from "components/Testimonials/Testimonials";
import ProductVariations from "components/ProductVariations/ProductVariations";
import Warranty from "components/Warranty/Warranty";
import CommonQuestions from "components/CommonQuestions/CommonQuestions";
import Section1 from '../components/Sections/Section1'
import Section2 from '../components/Sections/Section2'
import Footer from '../components/Footer/Footer'

export default () => {

  const [productId, setProductId] = useState()
  const [showBlocks, setShowBlocks] = useState({})
  const [color, setColor] = useState('')

  useEffect(()=>{
    const url = window.location.href.split('/')
    const id = parseInt(url[url.length-1])
    axios.get(`${baseApiUrl}/product/${id}/${pageTest}`)
    .then(res => {
      setShowBlocks(res.data)
      setColor(res.data.cor)
      setProductId(id)
    })
  },[])

  return (
    <>
      <input id="cts-productId" type="hidden" name="cts-productId" value={productId} />
      {showBlocks.showStore && showBlocks.showProduct &&
      <>
        {showBlocks.mainStore && <Header1/>}
        {showBlocks.exibir_frete_gratis === 'Sim' && <Header2 color={color} />}
        <AnimationRevealPage>
          <ProductSlider productId={productId} color={color} />
          {showBlocks.exibir_mensagem_de_urgencia === 'Sim' && <Alert color={color}/>}
          {showBlocks.showVideo && <ProductVideo productId={productId} color={color} />}
          {showBlocks.showPositives && <Positives productId={productId} color={color} />}
          <BuyButton color={color} />
          {showBlocks.showDescription && <LongDescription productId={productId} />}
          {showBlocks.showTestimonials && <Testimonials productId={productId} color={color} />}
          {showBlocks.showVariations && <ProductVariations productId={productId} color={color} />}
          <Warranty productId={productId} color={color} />
          {showBlocks.showQuestions && <CommonQuestions productId={productId} color={color} />}
          <Section1 color={color} />
          <Section2 color={color} />
          <Footer />
        </AnimationRevealPage>
      </>
      }
      {!showBlocks.showStore &&
        <span>Esta loja no existe!<br></br></span>
      }
      {!showBlocks.showProduct &&
        <span>Este produto no existe!</span>
      }
    </>
  )
}
