import React, {useEffect, useState} from 'react'
import './CarouselView.css'
import axios from "axios"
import { baseApiUrl, pageTest } from '../../config/global'

import { Carousel } from 'react-bootstrap'

export default function CarouselView(){

	const [banners, setBanners] = useState([])
	const getBanners = () => axios.get(`${baseApiUrl}/banners/${pageTest}`).then(res => setBanners(res.data))
	useEffect(()=>{getBanners()}, [])

	return (
		<>
			{banners.length > 0 &&
			<div id="carousel-teste">
					
					<Carousel>
						{banners.map((banner, index) => 
						<Carousel.Item key={index}>
							<img
							className="w-100"
							src={`https://app.shopifacil.com.br/banners/imagem/${banner.id}.${banner.imagem}`}
							alt={`Banner-${index}`}
							/>
						</Carousel.Item>
						)}
					</Carousel>
			</div>
			}
		</>
  )
}